body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.flexLogin {
  display: flex;
  justify-content: center;
  height: 100vh;
  width: 100%;
  align-items: center;
}

.login {
  flex: 1 1;
  max-width: 500px;
}
.sideNav {
    background: #1e1d1d;
    width: 250px;
    height: 100vh;
    color: #fff;
    -webkit-transition: width .3s;
    transition: width .3s;
    position: fixed;
    left: 0;
}

.sideNav ul {
    list-style: none;
    padding: 0;
}

.sideNav ul li {
    padding: 1rem 1.5rem;
    border-left: 3px solid transparent;
}

.sideNav ul li.active {
    border-left: 3px solid #d53737;
}

.main {
    display: flex;
}

.sideNav a {
    color: #dfdfdf
}

.sideNav a:hover {
    color: #a8a8a8
}

.content {
    color: #dfdfdf;
    padding-left: 280px;
    flex: 1 1;
}

.img-rounded {
    border-radius: 50%;
}

.row .flex {
    width: 225px;
    padding: 1rem 0;
}
.flex {
    display: flex;
}

.flex.fill {
    flex: 1 1;
}

img.w75 {
    width: 75px;
}

img.w50 {
    width: 50px;
}

.fill.avatar {
    align-self: center;
    padding-left: 5px;
}

body {
    overflow-x: hidden;
}

span.online {
    color: #65d365;
}

.color_green {
    color: #65d365;
}

.color_red {
    color: #d53737;
}

span.online::before {
    content: "Trực Tuyến"
}

span.offline {
    color: #6c6969;
}

span.offline::before {
    content : "Ngoại Tuyến"
}

body.modal-open {
    overflow-x: hidden;
}

select {
    display: block!important;
}

input[type=checkbox] {
    opacity: 1 !important;
    position: relative !important;
    cursor: pointer;
    pointer-events: all !important;
}

label[for] {
    cursor: pointer;
}

tr.active {
    background: rebeccapurple;
    -webkit-transition: background .5s;
    transition: background .5s;
}

.table a {
    color: #41b0ee !important;
}

.tke {
    background: #e0e0e0;
    float: right;
    padding: .2rem .5rem;
    border-radius: 5rem;
    font-size: small;
    font-weight: bold;
    color: black;
}

.shipment {
    float: right;
    font-size: large;
    color: burlywood;
}
